<template>
    <div class="expadable">
        <div class="card catalog-item" v-for="(item, index) in value" :key="index">
            <div>
                <strong>
                    <a href="/" v-if="expanded[index]" @click.prevent="() => {collapse(index)}">
                        <span class="expand">-</span>
                        <span
                            v-if="item[titlefield]"
                            v-html="item[titlefield].replace('<br/>', ' ')"
                        ></span>
                        <span v-else>Элемент {{index + 1}}</span>
                    </a>
                    <a href="/" v-else @click.prevent="() => {expand(index)}">
                        <span class="expand">-</span>
                        <span
                            v-if="item[titlefield]"
                            v-html="item[titlefield].replace('<br/>', ' ')"
                        ></span>
                        <span v-else>Элемент {{index + 1}}</span>
                    </a>
                </strong>
                <div class="button-delete" v-if="index !== 0 || objTemplate">
                    <b-button size="sm" variant="danger" @click="()=> {deleteItem(index)}">
                        <i class="fa fa-trash"></i> Удалить
                    </b-button>
                </div>
            </div>

            <div class="content" v-show="expanded[index]">
                <slot name="content" :item="item" :index="index"></slot>
            </div>
        </div>
        <div class="add-new-item">
            <b-button size="sm" variant="success" @click="()=> {addItem()}">
                <i class="fa fa-plus"></i> Добавить
            </b-button>
        </div>
    </div>
</template>

<script>
export default {
    name: "FormEditableList",
    props: {
        titlefield: {
            default: "title"
        },
        title: String,
        value: Array,
        index: Number,
        objTemplate: Object
    },
    data() {
        return {
            expanded: [],
            list: []
        };
    },
    methods: {
        expand(index) {
            this.expanded[index] = true;
            this.expanded = [...this.expanded];
        },
        collapse(index) {
            this.expanded[index] = false;
            this.expanded = [...this.expanded];
        },
        addItem() {
            console.log(this);
            console.log(this.objTemplate);
            if (this.objTemplate) {
                this.list.push({ ...this.objTemplate });
            } else {
                this.list.push({ ...this.list[0] });
            }
            this.expanded = [];
            this.list.forEach(() => {
                this.expanded.push(false);
            });
            this.$emit("input", this.list);
        },
        deleteItem(index) {
            this.list.splice(index, 1);
            this.expanded = [];
            this.list.forEach(() => {
                this.expanded.push(false);
            });
            this.$emit("input", this.list);
        }
    },
    watch: {
        value() {
            this.list = this.value;
            this.expanded = [];
            if (this.list) {
                this.list.forEach(() => {
                    this.expanded.push(false);
                });
            }
        }
    },
    created() {
        console.log(this.value);
        this.list = this.value;
        this.expanded = [];
        if (this.list) {
            this.list.forEach(() => {
                this.expanded.push(false);
            });
        }
    }
};
</script>

<style lang="scss" >
.button-delete {
    float: right;
}
.content {
    margin-top: 1em;
}
.expand {
    display: inline-block;
    width: 15px;
}
</style>