<template>
    <div>
        <strong>
            <a href="/" v-if="expanded" @click.prevent="() => {expanded = false}">
                <span class="expand">-</span>
                <span>{{i18n.get(title)}}</span>
            </a>
            <a v-else href="/" @click.prevent="() => {expanded = true}">
                <span class="expand">+</span>
                <span>{{i18n.get(title)}}</span>
            </a>
        </strong>
    </div>
</template>

<script>
import i18n from "../../../i18n.js";
export default {
    name: "BlockTitle",
    props: {
        title: String,
        value: Boolean
    },
    data() {
        return {
            expanded: false,
            newValue: "",
            i18n: i18n
        };
    },
    watch: {
        expanded() {
            this.expanded = this.expanded;
            this.$emit("input", this.expanded);
        }
    },
    created() {
        this.expanded = this.value;
    }
};
</script>

<style lang="scss" scoped>
.expand {
    display: inline-block;
    width: 1em;
}
</style>