<template>
    <b-form-group :label="i18n.get(title)" :label-cols="5" class="col-12">
        <b-form-input
            class="col-sm-12"
            v-model="newValue"
            type="text"
            :placeholder="i18n.get(title)"
        ></b-form-input>
    </b-form-group>
</template>

<script>
import i18n from "../../../i18n.js";
export default {
    name: "FormInputNumber",
    props: {
        title: String,
        value: Number
    },
    data() {
        return {
            newValue: "",
            i18n: i18n
        };
    },
    watch: {
        newValue() {
            this.$emit("input", Number.parseFloat(this.newValue));
        }
    },
    created() {
        this.newValue = this.value.toString();
    }
};
</script>

