<template>
    <b-form-group :label="i18n.get(title)" :label-cols="5" class="col-12">
        <b-form-input
            class="col-sm-12"
            v-model="newValue"
            :type="type"
            :placeholder="i18n.get(title)"
            :state="state"
            :id="id"
        ></b-form-input>
        <b-form-invalid-feedback :id="id" v-html="error"></b-form-invalid-feedback>
    </b-form-group>
</template>

<script>
import i18n from "../../../i18n.js";
export default {
    name: "FormInputText",
    props: {
        title: String,
        value: String,
        error: String,
        state: { default: null },
        type: {
            default: "text"
        }
    },
    data() {
        return {
            id: null,
            newValue: "",
            i18n: i18n
        };
    },
    watch: {
        newValue() {
            this.$emit("input", this.newValue);
        }
    },
    created() {
        this.newValue = this.value;
    },
    mounted() {
        this.id = "input_" + this._uid;
    }
};
</script>

