
<template>
    <b-form-group :label="i18n.get(title)" :label-cols="5" class="col-12">
        <b-form-select
            @change="onChange"
            :value="value"
            :options="options"
            class="col-sm-12"
            type="text"
            :placeholder="i18n.get(title)"
        ></b-form-select>
    </b-form-group>
</template>

<script>
import i18n from "../../../i18n.js";
export default {
    name: "FormSelect",
    props: {
        title: String,
        value: String,
        options: Array
    },
    data() {
        return {
            newValue: "",
            i18n: i18n
        };
    },
    watch: {
        newValue() {
            console.log(this.newValue);
            this.$emit("change", this.newValue);
        }
    },
    methods: {
        onChange(val) {
            this.newValue = val;
        }
    },
    created() {
        this.newValue = this.value;
    }
};
</script>

