<template>
    <b-form-group :label="i18n.get(title)" :label-cols="5" class="col-12">
        <b-form-textarea
            class="col-sm-12"
            :value="newValue"
            @input="(val) => {newValue = val}"
            type="text"
            max-rows="6"
            rows="6"
            :placeholder="i18n.get(title)"
        ></b-form-textarea>
    </b-form-group>
</template>

<script>
import i18n from "../../../i18n.js";
export default {
    name: "FormTextarea",
    props: {
        title: String,
        value: String
    },
    data() {
        return {
            newValue: "",
            i18n: i18n
        };
    },
    watch: {
        newValue() {
            this.$emit("input", this.newValue);
        }
    },
    created() {
        this.newValue = this.value;
    }
};
</script>

<style lang="scss" scoped>
textarea {
    resize: none;
}
</style>